import {GET_PROJECT_REQUST,
        GET_PROJECT_SUCCESS,
        GET_PROJECT_FAIL} from '../actions/ProjectActions'

const initialState = {
    name:"–ù–µ–∏–∑–≤–µ—Å—Ç–Ω–æ",
    discription:"–Ω–µ—Ç",
    date–°reat:"-",
    isLoading: false,
    subproject:[],
    error:''
}

export function projectReducer(state=initialState, action){
    switch(action.type){
        case GET_PROJECT_REQUST:
            return{
                ...state,
                error:"",
                isLoading:true
            }
        case GET_PROJECT_SUCCESS:
            return{
                ...state,
                error:"",
                isLoading:false,
                name: action.payload.name,
                discription: action.payload.discription,
                date–°reat: action.payload.date–°reat
            }
        default:
            return state
    }
}